import React from 'react';
//import Toaster from "core/components/toaster";
import isEmpty from "lodash/isEmpty";
import { useState } from "react";
//import LaddaButton, { ZOOM_OUT } from "react-ladda";
import { withTranslate } from "react-redux-multilingual";
import { Container, Row } from "reactstrap";
import FormikWrapper from "./FormikWrapper";

const removeEmpty = (obj) => {
  Object.keys(obj).map((key) => {
    if (obj[key] === "") {
      delete obj[key];
    }
    return obj;
  });
  return obj;
};

const FilterSearchWrapper = ({
  initialValues,
  onSubmit,
  children,
  onQueryParamChanged,
  rowsPerPage,
  queryParams,
  onToggle,
  ...rest
}) => {
  const [zoomOut, setZoomOut] = useState(false);

  const handleResponse = ({ res }) => {
    const { data = {} } = res;
    const { error, error_description, status } = data;

    if (!isEmpty(error) && error === "error") {
      const {
        errors: { errors },
      } = data;
      if (errors) {
        // errors.map((err) => Toaster(get(err, "message", "Error"), "error"));
      } else {
        //Toaster(`${status} - ${error_description}`, "error");
      }
    }
  };

  const search = (params) => {
    params = removeEmpty(params);
    setZoomOut(true);
    onSubmit({ ...params, per_page: rowsPerPage })
      .then((res) => {
        setZoomOut(false);
        onToggle && onToggle(false);
        handleResponse({ res });
      })
      .catch((err) => {
        setZoomOut(false);
        onToggle && onToggle(false);
        handleResponse({ res: err });
      });
  };

  return (
    <FormikWrapper
      initialValues={!isEmpty(queryParams) ? queryParams : initialValues}
      onSubmit={search}
      {...rest}
      mode="search"
    >
      <Container fluid className="mx-md-0 p-0">
        <Row form className="pl-md-1">
          {children}
          {/* <Col xs="12" sm="12" md="3" xl="2">
            <LaddaButton
              className="btn btn-primary btn-hover-shine btn-sm btn-block z-index-0"
              size="sm"
              block
              loading={zoomOut}
              data-style={ZOOM_OUT}
              color="primary"
              type="submit"
            >
              <i
                className="fa fa-fw"
                aria-hidden="true"
                title="Copy to use refresh"
              >
                
              </i>
            </LaddaButton>
          </Col> */}
        </Row>
      </Container>
    </FormikWrapper>
  );
};

export default withTranslate(FilterSearchWrapper);
