import * as Yup from "yup";
export const ValidationSchema = () => {
  return Yup.object().shape({
    property_address: Yup.string("Required"),
    unit_number: Yup.string().required("Required"),
    advert_type: Yup.string().required("Required"),
    furnishing_options: Yup.string().required("Required"),
    property_type: Yup.string().required("Required"),
    number_of_beds: Yup.string().required("Required"),
    number_of_bathrooms: Yup.string().required("Required"),
    // minimum_tenancy_length: Yup.string().required("Required"),
    // monthly_rent: Yup.string().required("Required"),
    // maximum_number_of_tenants: Yup.string().required("Required"),
    // security_deposit: Yup.string().required("Required"),
    // availability_for_remote_video_viewing: Yup.boolean().oneOf([true], "Required"),
    // generate_description: Yup.string(),
    // availability_for_viewing_note: Yup.string().required("Required"),
    terms: Yup.boolean().oneOf([true], "Required"),
    // earliest_move_in_date: Yup.date().required("Required"),

    youtube_uri: Yup.string().matches(
      /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|v=|\?v=)([^#]*).*/,
      "Enter correct Youtube url!"
    ),
  });
};
