import React from 'react';
import isNil from "lodash/isNil";
import {
  AllFilters,
  Animiteis,
  AnyPrice,
  Bedsbath,
  Pets,
  RightSide,
  Searchfield,
  Submit,
  ToolbarWrapper
} from "./Toolbar.style";

export default function Toolbar({
  aminiteis,
  allFilters,
  BedsBath,
  pets,
  commute,
  anyprice,
  Search,
  SubmitButton,
  right,
  className,
  filter,
}) {
  // Add all classs to an array
  const addAllClasses = ["toolbar"];

  // className prop checking
  if (className) {
    addAllClasses.push(className);
  }

  return (
    <ToolbarWrapper className={addAllClasses.join(" ")}>
      {filter && filter}
      {Search && <Searchfield className="searchfield">{Search}</Searchfield>}
      {aminiteis && <Animiteis>{aminiteis}</Animiteis>}
      {allFilters && <AllFilters>{allFilters}</AllFilters>}
      {BedsBath && <Bedsbath>{BedsBath}</Bedsbath>}
      {pets && <Pets>{pets}</Pets>}
      {/* {commute && <Commute>{commute}</Commute>} */}
      {anyprice && <AnyPrice>{anyprice}</AnyPrice>}
      {SubmitButton && <Submit>{SubmitButton}</Submit>}
      {!isNil(right) && (
        <RightSide className="toolbar_right__side">{right}</RightSide>
      )}
    </ToolbarWrapper>
  );
}
